.photos {
  display: flex;
  justify-content: space-between;
  max-width: 1260px;

  &__img {
    width: calc(50% - 10px);
    height: 256px;
    object-fit: cover;
    object-position: center;
    border-radius: 8px;
  }

  &__img_1,
  &__img_6 {
    width: calc(67% - 10px);
  }

  &__img_2,
  &__img_5 {
    width: calc(33% - 10px);
  }
}

@media screen and (max-width: 640px) {
  .photos {
    &__img_2,
    &__img_5,
    &__img_1,
    &__img_6 {
      width: calc(50% - 10px);
    }
  }
}
